import React from 'react'

class UnirseProtocolo extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            usuario: this.props.usuario
        }
    }

    render() {
        return (
            <div>
                <h1>¿Desea unirse a un protocolo?</h1>
                <h2>Ingrese el número del protocolo</h2>
                <input type='numTT'
                    placeholder='             Número TT' />
            </div>
        );
    }
}

export default UnirseProtocolo