import React from 'react'

class EvaluacionProtocolo extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            usuario: this.props.usuario
        }
    }

    render() {
        return (
            <div>
                <h1>Evaluación de TT</h1>
                <h2>La calificación de su trabajo es:</h2>
            </div>
        );
    }
}

export default EvaluacionProtocolo