import React from 'react'

class EstadoProtocolo extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            usuario:this.props.usuario
        }
    }

    render() {
        return (
            <div>
            <h1>Estado de TT</h1>
            <h2>Su protocolo actualmente se encuentra:</h2>
        </div>
        );
    }
}

export default EstadoProtocolo